import React from "react";
import { Redirect, useLocation } from "react-router-dom";
import { ROUTE_PATHS } from "./route-paths";

const RedirectToHelp = () => {
    const location = useLocation();
    let redirectPath = location.pathname;

    if (location.pathname.includes(ROUTE_PATHS.support)) {
        redirectPath = location.pathname.replace(ROUTE_PATHS.support, ROUTE_PATHS.help);
    }

    if (location.pathname.includes(ROUTE_PATHS.supportCategory)) {
        redirectPath = location.pathname.replace(ROUTE_PATHS.supportCategory, ROUTE_PATHS.help);
    }

    return <Redirect to={redirectPath} />;
};

export default RedirectToHelp;
